import React, {useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { connect } from 'react-redux';
import {jsonHash, providerSign} from '../lib/core';

const useStyles = makeStyles(theme => ({
  header: {
    fontSize: theme.spacing(2),
    position: 'fixed',
    top: 0
  },
  body: {
    marginTop: theme.spacing(5),
    marginLeft: theme.spacing(4),
    marginRight: theme.spacing(4)
  },
  textField: {
    width: theme.spacing(50)
  },
  line: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center'
  }
}));

const Index = function({provider}) {
  const classes = useStyles();
  const [inputObj, setInputObj] = useState('');
  const [sig, setSig] = useState('abc');

  return (
    <React.Fragment>
      <div className={classes.header}>My Ethereum Tools</div>
      <div className={classes.body}>
        <div className={classes.line}>
          <TextField
            id='input-box'
            label='Input Object'
            className={classes.textField}
            value={inputObj}
            onChange={e => setInputObj(e.target.value)}
            margin='normal'
            variant='outlined'
          />
          <Button
            variant='outlined'
            className={classes.btn}
            onClick={_ => providerSign(provider, jsonHash(inputObj)).then(setSig)}>
            Sign this JSON
          </Button>
          <TextField
            disabled
            id='output-box'
            label='Signature'
            className={classes.textField}
            value={sig}
            margin='normal'
            variant='outlined'
          />
        </div>
      </div>
    </React.Fragment>
  );
};

export default connect(({provider}) => ({provider}))(Index);
